.button-container {
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: #fff;
  border-radius: 25px;
  border: 2px solid white;
  cursor: pointer;
  user-select: none;
  margin: 4px;
}

.operador {
  background-color: #00471b;
}

.button-container:hover {
  background-color: #5a01a7;
}

.operador:hover {
  background-color: #ff6600;
}